.sidebar {

}


.open {
    background-color: white;
    width: 350px;
    height: 100%;

}

.open:hover {
    color: deepskyblue;
}

.sidebar.open {
    position: absolute;
    z-index: 10;

}

.background {
    z-index: 10;
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.3;
}

.nav {

}

.navHidden {
    display: none;
}

.nav a {
    display: flex;
    width: 250px;
    height: 50px;
    flex-direction: column;
    font-size: 30px;
    color: black;
    text-decoration: none;
    text-align: center;
    border: 1px solid black;
    border-radius: 10px;
    margin-left: 20px;
    margin-top: 5px;

}

.nav a:hover {
    color: dodgerblue;
}

.active {
    color: white;
    background-color: #b7b7b7;

}


.close {
    margin: 12px 0 0 300px;
    border: none;
    background: none;
}

.close:hover {
    background: #c9c9c9;
    border-radius: 5px;

}

